body {
  color: #e0e0e0;
  background: linear-gradient(120deg, #4c5c96, #6b4f8d);
  flex-direction: column;
  align-items: center;
  min-height: 100vh;
  margin: 0;
  font-family: Segoe UI, Tahoma, Geneva, Verdana, sans-serif;
  display: flex;
  overflow-x: hidden;
}

header {
  text-align: center;
  padding: 20px;
}

header h1 {
  margin: 0;
  font-size: 2.5rem;
  font-weight: 600;
}

header p {
  color: silver;
  margin: 5px 0 0;
  font-size: 1.2rem;
}

.main-container {
  box-sizing: border-box;
  justify-content: center;
  align-items: flex-start;
  gap: 30px;
  width: 100%;
  max-width: 1400px;
  padding: 20px;
  display: flex;
}

.game-wrapper {
  flex-basis: 70%;
  justify-content: center;
  display: flex;
  position: relative;
}

.level-complete-banner {
  color: #fff;
  z-index: 10;
  text-align: center;
  background-color: #000000b3;
  border-radius: 10px;
  padding: 20px 40px;
  font-size: 2.5rem;
  font-weight: bold;
  animation: 3s ease-in-out forwards fadeInOut;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@keyframes fadeInOut {
  0% {
    opacity: 0;
    transform: translate(-50%, -50%)scale(.8);
  }

  20% {
    opacity: 1;
    transform: translate(-50%, -50%)scale(1);
  }

  80% {
    opacity: 1;
    transform: translate(-50%, -50%)scale(1);
  }

  100% {
    opacity: 0;
    transform: translate(-50%, -50%)scale(.8);
  }
}

canvas {
  background-color: #2c2c2c;
  border-radius: 15px;
  max-width: 100%;
  height: auto;
  box-shadow: 0 10px 20px #0003;
}

.info-wrapper {
  flex-direction: column;
  flex-basis: 30%;
  gap: 20px;
  display: flex;
}

.info-panel {
  backdrop-filter: blur(10px);
  background-color: #ffffff1a;
  border: 1px solid #fff3;
  border-radius: 15px;
  padding: 20px;
  box-shadow: 0 5px 15px #0000001a;
}

.info-panel h3 {
  border-bottom: 1px solid #ffffff4d;
  margin-top: 0;
  margin-bottom: 15px;
  padding-bottom: 10px;
  font-size: 1.3rem;
  font-weight: 500;
}

.stats {
  flex-direction: column;
  gap: 10px;
  display: flex;
}

.info-item {
  justify-content: space-between;
  font-size: 1rem;
  display: flex;
}

.info-item span:first-child {
  color: silver;
}

.buttons {
  gap: 10px;
  margin-top: 20px;
  display: flex;
}

button {
  cursor: pointer;
  border: none;
  border-radius: 8px;
  flex-grow: 1;
  padding: 12px 20px;
  font-size: 1rem;
  font-weight: 600;
  transition: all .2s ease-in-out;
}

#start-btn {
  color: #fff;
  background-color: #4caf50;
}

#start-btn:hover {
  background-color: #5cb85c;
}

#reset-btn {
  color: #fff;
  background-color: #f44336;
}

#reset-btn:hover {
  background-color: #ff5a4d;
}

@media (width <= 900px) {
  .main-container {
    flex-direction: column;
    align-items: center;
  }

  .info-wrapper, .game-wrapper {
    width: 100%;
    max-width: 800px;
  }
}

@media (width <= 1200px) {
  .game-container {
    grid-template-columns: 1fr;
    gap: 15px;
  }

  .game-info, .ai-info {
    order: 2;
  }

  #gameCanvas {
    order: 1;
  }
}

@media (width <= 768px) {
  .container {
    padding: 10px;
  }

  header h1 {
    font-size: 2rem;
  }

  #gameCanvas {
    width: 100%;
    height: auto;
  }
}

@keyframes pulse {
  0% {
    transform: scale(1);
  }

  50% {
    transform: scale(1.05);
  }

  100% {
    transform: scale(1);
  }
}

.btn:active {
  animation: .2s ease-in-out pulse;
}
